	module.exports = {"pathContext":{"page":{"fileAbsolutePath":"/Users/smoclock/key/cdn-docs/src/docs/000-config/008-8.md","html":"<p>白山支持针对单个请求url进行限速，通过限速该功能，您可以对域名整体带宽峰值进行控制。</p>\n<h3 id=\"配置指引\"><a href=\"#%E9%85%8D%E7%BD%AE%E6%8C%87%E5%BC%95\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>配置指引</h3>\n<ol>\n<li>\n<p>登录控制台，在左侧菜单栏域名管理内选择【域名配置】，选择域名，再选择【添加配置】。点击【添加配置】，选择【特殊功能】内的添加【限速】</p>\n<p><img src=\"/image/image-20181121110652535.png\" alt=\"image-20181121110652535\"></p>\n<p><img src=\"/image/image-20181121110728072.png\" alt=\"image-20181121110728072\"></p>\n<p><img src=\"/image/image-20181121110744492.png\" alt=\"image-20181121110744492\"></p>\n</li>\n<li>\n<p>配置添加完成后点击【请点击发布】，再【确认保存】，配置即可生效</p>\n<p><img src=\"/image/image-20181121111032131.png\" alt=\"image-20181121111032131\"></p>\n<p><img src=\"/image/image-20181121111052544.png\" alt=\"image-20181121111052544\"></p>\n<p><img src=\"/image/image-20181121111119882.png\" alt=\"image-20181121111119882\"></p>\n</li>\n<li>\n<p>配置参数含义：</p>\n<ul>\n<li>前面多少字节不限速：为保障用户体验，限速规则可针对文件请求前一段不限速。例如用户请求100M的文件，可设置前12M不进行限速，12M之后的请求根据限速值限速。该参数为非必填项。如果不填写，默认值为0，表示整个文件都限速。</li>\n<li>限速值：限速值为必填项，参数值必须大于等于0。</li>\n</ul>\n</li>\n<li>\n<p>注意事项：</p>\n<ul>\n<li>该功能是针对域名下的所有请求都进行限速，如果需要针对指定的url限速，请联系白山相关同事。</li>\n</ul>\n</li>\n</ol>\n<h3 id=\"配置案例\"><a href=\"#%E9%85%8D%E7%BD%AE%E6%A1%88%E4%BE%8B\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>配置案例</h3>\n<p>域名Blog4.lowxp.com针对文件前5M不限速，超过5M后限速600KB/s</p>\n<p><img src=\"/image/image-20181121112155875.png\" alt=\"image-20181121112155875\"></p>\n<p>用户请求80M文件 <a href=\"http://Blog4.lowxp.com/2.mp4%EF%BC%8C%E5%88%99%E7%94%A8%E6%88%B7%E8%AF%B7%E6%B1%820-5M%E5%86%85%E4%B8%8D%E5%81%9A%E9%99%90%E9%80%9F%EF%BC%8C5M-80M%E9%99%90%E9%80%9F600KB/s%E3%80%82\">http://Blog4.lowxp.com/2.mp4，则用户请求0-5M内不做限速，5M-80M限速600KB/s。</a></p>","headings":[{"value":"配置指引","depth":3},{"value":"配置案例","depth":3}],"frontmatter":{"title":"限速规则","parent":"/Users/smoclock/key/cdn-docs/src/docs/000-config/008-8.md absPath of file"}},"nav":[{"title":"用户指南","children":[{"title":"源站配置","path":"/docs/config/1"},{"title":"回源HOST","path":"/docs/config/2"},{"title":"缓存规则","path":"/docs/config/3"},{"title":"IP黑名单","path":"/docs/config/4"},{"title":"Referer黑白名单","path":"/docs/config/5"},{"title":"HTTP头配置","path":"/docs/config/6"},{"title":"跟随301/302跳转","path":"/docs/config/7"},{"title":"限速规则","path":"/docs/config/8"}],"redirectFrom":"/docs/config","path":"/docs/config/1"}]}}

